import React from 'react';
import PropTypes from 'prop-types';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
const FootCustomComponent = (props) => {
    let {id, setProps, data} = props;
    console.log("data before parsing", data);
    if(data === undefined || data === 'data') {
        return <div>Waiting for data</div>
    }
    data = JSON.parse(data);
    console.log("data after parsing", data);

    const circleStyle = {
        width: '75px',
        height: '75px',
        borderRadius: '50%',
        border: '1px solid #000',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: '#000',
        fontSize: '18px',
    };

    return (
        <div id={id} style={{display: "flex"}}>
            <div style={{position: 'relative', height: '200px', width: '200px', margin: "20px"}}>
                <div style={{position: 'absolute', top: '0', left: '0'}}>
                    <div style={{...circleStyle,
                        backgroundColor: data[0]["anomaly"] ? '#ee3737' : '#dcdbdb',
                    }}>{data[0]["value"]}</div>
                </div>

                <div style={{position: 'absolute', top: '0', right: '0'}}>
                    <div style={{...circleStyle,
                        backgroundColor: data[1]["anomaly"] ? '#ee3737' : '#dcdbdb',
                    }}>{data[1]["value"]}</div>
                </div>

                <div style={{position: 'absolute', bottom: '0', left: '50%', transform: 'translateX(-50%)'}}>
                    <div style={{...circleStyle,
                        backgroundColor: data[2]["anomaly"]? '#ee3737' : '#dcdbdb',
                    }}>{data[2]["value"]}</div>
                </div>
            </div>
            <div style={{position: 'relative', height: '200px', width: '200px', margin: "20px"}}>
                <div style={{position: 'absolute', top: '0', left: '0'}}>
                    <div style={{...circleStyle,
                        backgroundColor: data[3]["anomaly"] ? '#ee3737' : '#dcdbdb',
                    }}>{data[3]["value"]}</div>
                </div>

                <div style={{position: 'absolute', top: '0', right: '0'}}>
                    <div style={{...circleStyle,
                        backgroundColor: data[4]["anomaly"] ? '#ee3737' : '#dcdbdb',
                    }}>{data[4]["value"]}</div>
                </div>

                <div style={{position: 'absolute', bottom: '0', left: '50%', transform: 'translateX(-50%)'}}>
                    <div style={{...circleStyle,
                        backgroundColor: data[5]["anomaly"] ? '#ee3737' : '#dcdbdb',
                    }}>{data[5]["value"]}</div>
                </div>
            </div>

        </div>
    );
}

FootCustomComponent.defaultProps = {};

FootCustomComponent.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * the string json encoded data from the sensors
     * Example:
     * [
     *             {
     *                 'anomaly': False,
     *                 'name': 'L0',
     *                 'value': 16
     *             },
     *             {
     *                 'anomaly': False,
     *                 'name': 'L1',
     *                 'value': 138
     *             },
     *             {
     *                 'anomaly': False,
     *                 'name': 'L2',
     *                 'value': 13
     *             },
     *             {
     *                 'anomaly': False,
     *                 'name': 'R0',
     *                 'value': 1023
     *             },
     *             {
     *                 'anomaly': False,
     *                 'name': 'R1',
     *                 'value': 1023
     *             },
     *             {
     *                 'anomaly': False,
     *                 'name': 'R2',
     *                 'value': 896
     *             }
     * ]
     */
    data: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default FootCustomComponent;
